.postDetails-other {
  background-color: rgba(245, 245, 245, 0.1);
  color: var(--text-bright);
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  height: auto;
}

.otherPost-header,
.mentionUser-header,
.postHashtag-header {
  height: auto;
  padding-bottom: 0.5rem;
  align-self: center;
  color: var(--button);
}

.mentionUser-header,
.postHashtag-header {
  margin-top: 2rem;
}

.post-bookmark {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  padding: 0.5rem;
  width: 100%;
}

.post-bookmark:hover {
  color: var(--button);
}

.post-bookmark-icon {
  padding-right: 0.5rem;
}

.mentionUser-container,
.postHashtag-container {
  display: flex;
  align-items: center;
  padding: 0.5rem;
  height: auto;
  max-width: 100%;
  flex-wrap: wrap;
  gap: 0.5rem;
}

@media only screen and (max-width: 768px) {
  .postDetails-other {
    background-color: var(--sidebar-background-mobile);
  }
}
